body {
    /* background-color: black; */
    color: white;
    font-family: "Roboto", sans-serif;
    display: -webkit-flex;
    display: flex;
    /* align-items: center; */
    -webkit-justify-content: center;
            justify-content: center;
    min-height: 100vh;
    padding: 2rem;
  }
  
  .board {
    /* border: 1px solid black; */
    background-color:white;
    width: 100%;
    height: 100%;
    display: grid;
    grid-template: repeat(3, 1fr) / repeat(3, 1fr); 
    grid-gap: 0px; 
    gap: 0px; 
    /* margin: 2em; */
  }
  .squares {
    

    background-color: white;
    border: none;
    font-size: 5rem;
    /* font-weight: 800; */
    cursor: pointer;
    outline: none;
    border: 0.5px solid rgb(102, 99, 99);
    border-radius: 10px;
    margin: 1px;
    width: 1.02em;
    height: 1.2em;
  }
  
  .X {
    background: linear-gradient(45deg, rgba(5,88,178,1) 0%, rgba(0,201,211,1) 35%, rgba(118,255,255,1) 100%); -webkit-background-clip: text;
    color: transparent;
    font-weight:bolder;
    
  }
  .O {
    background: linear-gradient(45deg, rgba(255,107,0,1) 0%, rgba(246,171,64,1) 67%, rgba(255,224,178,1) 100%); -webkit-background-clip: text;
    color: transparent;
    font-weight:bolder;
  }
  
  .info-wrapper {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
  }
  
  h3 {
    margin-top: -15px;
    margin-bottom: -5px;
    color: black;
    text-align: center;
  }
  
  li {
    list-style: none;
  }
  
  .info-wrapper button {
    /* color: white; */
    border: none;
    outline: none;
    cursor: pointer;
    border: 1px solid rgb(117, 114, 114);
  }

  .board-container{
    border:1px solid;
    margin: 15px;
    padding: 20px;
    border-radius: 15px;
    box-shadow: 2px 5px 8px #888888;
  }

  .setting{
    margin-top: 1em;
    padding: -0.4em;
    height: 2em;
    width: 2em;
    border: none;
    border-radius: 2em;
    box-shadow: 2px 5px 8px #888888;
    font-size: 22px;
    
  }

